#recipe-view {
    display: flex;
    flex-direction: row;
    margin-top: 30px;
    height: 100vh;
}

.recipes-list {
    flex-basis: 0;
    flex-grow: 1;
    width: 50%;
    overflow-y: scroll;
    margin-left: 30px;
}

.recipe-detail {
    display: flex;
    flex-direction: column;
    width: 45%;
    margin-left: 25px;
    margin-right: 10px;
}

#wrapper {
    position: relative;
    padding-top: 10px;
}

#counter {
    display: block;
    position: absolute;
    right: 10px;
    top: 0;
}

#no-results {
    position: absolute;
    top: 25%;
    left: 40%;
}
