@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Megrim&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

body {
  color: #505050;
}
header {
    width: 100%;
    background-color: #2c9096;
    padding: 10px;
    padding-left: 0;
}

h1 {
    font-family: 'Bebas Neue';
    margin: 0;
    color: white;
    float: left;
    margin-left: 20px;
    position: absolute;
}

.container {
    display: flex;
}

.input.container {
    justify-content: center;
}

.input-field {
    color: white;
    height: 40px;
    width: 450px;
    padding-right: 70px;
    border-radius: 20px;
    border: none;
    background-color: #38b6bd;
    padding: 0;
    padding-left: 15px;
}

.input-field:focus, #add-button, #search-button, #delete-button {
    outline: none;
}

.input-field::-webkit-input-placeholder {
    color: white;
}

.input-field:-ms-input-placeholder {
    color: white;
}

.input-field::-ms-input-placeholder {
    color: white;
}

.input-field::placeholder {
    color: white;
}

#add-button {
    height: 40px;
    margin-left: -70px;
    width: 70px;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    border: white;
    background-color: white;
    color: #505050;
    line-height: 50%;
}

.list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    list-style: none;
    max-width: 470px;
    margin: 0 auto;
    margin-top: 10px;
}

.list li, #search-button {
    border-radius: 25px;
    background-color: #2c9096;
    padding-left: 20px;
    height: 35px;
    color: white;
    margin-right: 10px;
    margin-top: 10px;
    font-size: 14px;
    /*center text*/
    display: flex;
    align-items: center;
}

#delete-button {
    margin-right: 10px;
    padding-left: 10px;
    padding-right: 5px;
    background-color: transparent;
    border-style: none;
    color: white;
    align-self: flex-end;
    line-height: 30px;
}

#search-button {
    border: none;
    padding-right: 20px;
    font-weight: bold;
}

#search-button:hover {
    background-color: white;
    color: #2c9096;
    border: 1px solid #2c9096;
}

#search-button:disabled {
    background-color:#E8E8E8;
    pointer-events: none;
}
#recipe-view {
    display: flex;
    flex-direction: row;
    margin-top: 30px;
    height: 100vh;
}

.recipes-list {
    flex-basis: 0;
    flex-grow: 1;
    width: 50%;
    overflow-y: scroll;
    margin-left: 30px;
}

.recipe-detail {
    display: flex;
    flex-direction: column;
    width: 45%;
    margin-left: 25px;
    margin-right: 10px;
}

#wrapper {
    position: relative;
    padding-top: 10px;
}

#counter {
    display: block;
    position: absolute;
    right: 10px;
    top: 0;
}

#no-results {
    position: absolute;
    top: 25%;
    left: 40%;
}

h2 {
    font-family: 'Bebas Neue';
    font-weight: 100;
    font-size: 22px;
}

.recipe {
    border-radius: 10px;
    box-shadow: 0 0 3px 3px rgba(240,240,240, 0.6);
}

.recipe.item {
    display: flex;
    height: 170px;
    margin-top: 10px;
    position: relative;
    flex-shrink: 0;
}

.image-cropper {
    width: 200px;
    height: 170px;
    overflow: hidden;
    flex-shrink: 0;
}

.image-cropper img {
    height: 100%;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}

#open-recipe {
    border-radius: 25px;
    border: none;
    background-color: #2c9096;
    color: white;
    position: absolute;
    right: 3%;
    bottom: 10px;
    padding: 10px 20px;
    text-decoration: none;
    font-size: 14px;
}

#open-recipe:hover {
    background-color: white;
    color: #2c9096;
    border: 1px solid #2c9096;
}

#open-recipe:focus {
    outline: none;
}

.likes {
    margin-top: 10px;
    display: flex;
    flex-direction: row;
}

p {
    padding-left: 10px;
}

.description {
    display: flex;
    flex-direction: column;
    box-shadow: none;
    margin-top: 16px;
    margin-left: 20px;
    width: 60%;
}

.recipe-information ul {
    list-style-type: none;
}

.recipe-information ul li{
    padding-bottom: 10px;
}

.recipe-information {
    margin-bottom: 30px;
    padding-left: 20px;
}

.recipe-information h2 {
    padding: 20px 0 10px 0;
}

.img-cropper{
    width: 100%;
    height: 100px;
    overflow: hidden;
    border-bottom: 5px solid #2c9096;
}

.img-cropper img{
    object-fit: cover;
    width: 100%;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
}

.check {
    color: #2c9096;
}

.check, .cross {
    padding-right: 5px;
    vertical-align: middle;
}

#prep {
    padding-left: 20px;
}

.recipe-steps {
    padding: 10px 10px 20px 40px;
}

.recipe-steps ol li{
    padding-bottom: 10px;
    line-height: 20px;
}

